import * as NcComponents from './components'

let NcVueComponents = Vue => {
	Object.values(NcComponents).forEach((NcComponent) => {
		Vue.use(NcComponent)
	})
}

NcVueComponents.version = '__VERSION__'

export default NcVueComponents
export * from './components'
